.wrapper {
  display: flex;
  width: 100%;
  max-width: 1240px;
  padding-top: 180px;
}
  
.navigation {
  margin-right: 60px;
}
  
.list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 36px;
}
  
.button {
  background-color: transparent;
  border: 0;
  padding: 0;
  cursor: pointer;
}

.link {
  text-decoration: none;
  color: #8585ad;
  transition: all 0.3s ease;
}
  
.link_active {
  color: white;
}
  
.link:hover {
  opacity: 0.8;
}
  
.link:active {
  color: white;
}
  
.text {
  max-width: 320px;
  opacity: 0.4;
  margin-top: 80px;
}