.wrapper {
    display: flex;
    align-items: center;
    width: 100%;
    flex-direction: column;
    padding-top: 180px;
  }

  .content {
    width: 100%;
    box-sizing: border-box;
  }
  
  .content_page_form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 180px;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 88px;
    width: 480px;
  }
  
  .link {
    color: #4c4cff;
    text-decoration: none;
    transition: all 0.3s ease;
  }
  
  .link:hover {
    opacity: 0.8;
  }
  
  .link:active {
    color: white;
  }